import { Label } from "../common/components/ui/label"
import { Button } from "../common/components/ui/button"
import { Link } from "react-router-dom";
import { Field, Formik, Form } from "formik";
import * as Yup from "yup";
import { InputTextField } from "../common/InputTextField";
import { InputCheckbox } from "../common/Checkbox";
const initialState = {
  usernameOrEmail: "",
  password: "",
  remember: false,
};

const FormValidationSchema = Yup.object({
  usernameOrEmail: Yup.string().required("Full name is required"),
  password: Yup.string().required("password is required"),
});

const SignIn = (props) => {
  return (
    <div className="container mx-auto px-4 h-full">
      <div className="flex content-center items-center justify-center h-full">
        <div className="w-full lg:w-4/12 px-4">
          <div className="relative flex flex-col min-w-0 break-words w-full mb-6 shadow-lg rounded-lg bg-blueGray-200 border-0">
            <div className="rounded-t mb-0 px-6 py-6">
              <Formik
                initialValues={initialState}
                validationSchema={FormValidationSchema}
                onSubmit={(values, { setFieldValue }) => {
                  props?.handleSignIn(values);
                }}
              >
                {({ values, setFieldValue, touched, errors }) => {
                  return (
                    <Form>
                      {/* <Grid container spacing={2} mt={2}>
                  <Grid item xs={12}>
                    <Field
                      name={`usernameOrEmail`}
                      value={`${values.usernameOrEmail}`}
                      label="Email/Username"
                      placeholder="Email/Username"
                      required
                      component={InputTextField}
                      autoComplete="email"
                      fullWidth

                      //error={
                      //touchedItems?.apartmentUnitHouseNumber &&
                      //ADDRESS_DETAILS?.apartmentUnitHouseNumber
                      //}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Field
                      name={`password`}
                      value={`${values.password}`}
                      label="Password"
                      placeholder="Password"
                      type="password"
                      required
                      component={InputTextField}
                      autoComplete="password"
                      fullWidth

                      //error={
                      //touchedItems?.apartmentUnitHouseNumber &&
                      //ADDRESS_DETAILS?.apartmentUnitHouseNumber
                      //}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <Field
                      name={`remember`}
                      value={`${values.remember}`}
                      label="Remember me"
                      placeholder="Remember me"
                      component={InputCheckbox}
                      //error={
                      //touchedItems?.apartmentUnitHouseNumber &&
                      //ADDRESS_DETAILS?.apartmentUnitHouseNumber
                      //}
                    />
                  </Grid>

                  <Grid item xs={12} md={12}>
                    <Button
                      type="submit"
                      fullWidth
                      variant="contained"
                      sx={{ mb: 2 }}
                    >
                      Sign In
                    </Button>
                  </Grid>
                  <Grid container>
                    <Grid item xs>
                      <Link to="/forgot" variant="body2">
                        Forgot password?
                      </Link>
                    </Grid>
                    <Grid item>
                      <Link to="/signup" variant="body2">
                        {"Don't have an account? Sign Up"}
                      </Link>
                    </Grid>
                  </Grid>
                </Grid> */}
                      <div className="w-full max-w-sm space-y-2">
                        <div className="space-y-2 text-center">
                          <div className="mx-auto h-8 w-auto" />
                          <div className="space-y-2">
                            <h1 className="text-3xl font-bold">Welcome back</h1>
                            <p className="text-gray-500 dark:text-gray-400">Enter your email and password to sign in</p>
                          </div>
                        </div>
                        <div className="space-y-4">
                          <div className="space-y-2">
                            <Label htmlFor="email">Email</Label>
                            <Field
                              name={`usernameOrEmail`}
                              value={`${values.usernameOrEmail}`}
                              placeholder="Email/Username"
                              required
                              component={InputTextField}
                              autoComplete="email"
                              fullWidth
                            />
                          </div>
                          <div className="space-y-2">
                            <div className="space-x-2">
                              <Label htmlFor="password">Password</Label>

                            </div>
                            <Field
                              name={`password`}
                              value={`${values.password}`}
                              label="Password"
                              placeholder="Password"
                              type="password"
                              required
                              component={InputTextField}
                              autoComplete="password"
                              fullWidth

                            //error={
                            //touchedItems?.apartmentUnitHouseNumber &&
                            //ADDRESS_DETAILS?.apartmentUnitHouseNumber
                            //}
                            />
                          </div>
                          <div className="space-y-2">
                            <div className="flex items-center justify-between">
                              <Field
                                id="remember"
                                name={`remember`}
                                checked={`${values.remember}`}
                                type="checkbox"
                                label="Remember me"
                                component={InputCheckbox}
                                // fullWidth
                                className="mr-2" />
                              <Link className="text-blue-500" to="/forgot">
                                Forgot your password?
                              </Link>
                            </div>

                          </div>
                          <div className="space-y-2">
                            <Button className="w-full">Sign in</Button>
                          </div>
                          <div className="space-y-2 w-full" >
                            <Link className="text-blue-500" to="/signup">
                              Don't have an account? Sign Up
                            </Link>
                          </div>
                        </div>
                      </div>
                    </Form>
                  );
                }}
              </Formik>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SignIn;
