import React from "react";
import { Checkbox } from "./components/ui/checkbox";
import { Label } from "./components/ui/label"

export const InputCheckbox = ({ form, field, ...props }) => {
  // console.log(field)
  const { name, checked } = field;
  const { handleChange, handleBlur } = form;
  const { label, className, disabled, inputProps } = props;


  return (
    <div className="flex items-center">
      <Checkbox
        id={name}
        name={name}
        checked={checked}
        onCheckedChange={(checked) => {
          form.setFieldValue(name, checked)
        }}
        onChange={handleChange}
        onBlur={handleBlur}
        disabled={disabled}
        className={className}
        {...inputProps}
      />
      <Label htmlFor="remember">{label}</Label>
    </div>
  );
};
