import React, {  } from "react";
import LoginPage from "../components/LoginPage";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
import { authenticateUser, validateToken } from "../store/actions/LoginActions";
import { useSnackbar } from "notistack";
const Login = (props) => {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const handleSignIn = async (params) => {
    if (params != null) {
      try {
        const res = await props.authenticateUser(params);
        params?.remember &&
          localStorage.setItem("userDetails", JSON.stringify(res));
        enqueueSnackbar("Login Successful", { variant: "success" });
        navigate("/dashboard");
      } catch (err) {
        console.log(err);
        enqueueSnackbar(err, { variant: "error" });
      }
    }
  };
  return <LoginPage navigate={navigate} handleSignIn={handleSignIn} />;
};

function mapStateToProps(state) {
  return {
    userData: state.LoginReducer,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    authenticateUser: (params) => dispatch(authenticateUser(params)),
    validateToken: (params) => dispatch(validateToken(params)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Login);
